  <!-- eslint-disable max-len -->
<template>
   <li class="cart__item product">
              <div class="product__pic">
                <img :src="item.product.image" width="120" height="120" alt="item.product.title">
              </div>
              <h3 class="product__title">
                {{ item.product.title }}
              </h3>
              <span class="product__code">
                Артикул: {{ item.product.id }}
              </span>

              <div class="product__counter form__counter">
                <button type="button" aria-label="Убрать один товар" @click.prevent="dropProduct">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-minus"></use>
                  </svg>
                </button>
  <!-- eslint-disable-next-line -->
                <input type="text" v-model.number="amount" name="count">

                <button type="button" aria-label="Добавить один товар" @click.prevent="addProduct">
                  <svg width="10" height="10" fill="currentColor">
                    <use xlink:href="#icon-plus"></use>
                  </svg>
                </button>
              </div>

              <b class="product__price">
                {{ (item.amount * item.product.price | numberFormat)  }}
              </b>

              <button class="product__del button-del" type="button" aria-label="Удалить товар из корзины" @click.prevent="updateDeleteCartProduct(item.productId)">
                <svg width="20" height="20" fill="currentColor">
                  <use xlink:href="#icon-close"></use>
                </svg>
              </button>
            </li>
</template>
<script>

import { API_BASE_URL } from '@/config';
import numberFormat from '@/helpers/numberFormat';
import axios from 'axios';
import { mapActions, mapMutations } from 'vuex';

export default {
  filters: { numberFormat },
  props: ['item'],
  computed: {
    amount: {
      get() {
        return this.item.amount;
      },
      set(value) {
        this.$store.dispatch('updateCartProductAmount', { productId: this.item.productId, amount: value });
      },
    },
  },
  methods: {
    ...mapMutations({ deleteProduct: 'deleteCartProduct' }),
    ...mapActions(['updateDeleteCartProduct']),
    dropProduct() {
      if (this.amount > 1) this.$emit(this.amount -= 1);
    },
    addProduct() {
      this.$emit(this.amount += 1);
    },
    deleteCartProduct(context, productId) {
      context.commit('deleteCartProduct', productId);
      return axios // eslint-disable-next-line
        .delete(API_BASE_URL + '/api/baskets/products', {
          data: {
            productId,
          },
          params: {
            userAccessKey: context.state.userAccessKey,
          },
        });
    },
  },
};

</script>
