<template>
  <!-- eslint-disable max-len -->
  <main class="content container">
    <div class="content__top content__top--catalog">
      <h1 class="content__title">
        Каталог
      </h1>
      <span class="content__info">
        152 товара
      </span>
    </div>

    <div class="content__catalog">
      <ProductFilter :price-from.sync="filterPriceFrom" :price-to.sync="filterPriceTo" :category-id.sync="filterCategoryId" :color-id.sync="filterColorId"/>

      <section class="catalog">

        <div v-if="productsLoading">Загрузка товаров...</div>
        <div v-if="productsLoading">Произошла ошибка при загрузке товаров <button @click.prevent="loadProducts">Попробовать еще раз</button></div>

        <ProductList :products="products"/>

        <BasePagination v-model="page" :count="countProducts" :per-page="productsPerPage"/>

      </section>

    </div>
  </main>
  <!-- eslint-disable max-len -->
  <!--eslint is disabled for the current line -->
   <!-- eslint-enable max-len -->
</template>
  <!-- eslint-disable max-len -->
<script>

import ProductList from '@/components/ProductList.vue';

import BasePagination from '@/components/BasePagination.vue';

import ProductFilter from '@/components/ProductFilter.vue';

import axios from 'axios';
import { API_BASE_URL } from '@/config';

export default {
  components: { ProductList, BasePagination, ProductFilter },
  data() {
    return {
      filterPriceFrom: 0,
      filterPriceTo: 0,
      filterCategoryId: 0,
      filterColorId: 0,

      page: 1,
      productsPerPage: 3,

      productsData: null,
      productsColors: null,
      productsLoading: false,
      productsLoadingFailed: false,
    };
  },
  computed: {
    products() {
      return this.productsData// eslint-disable-next-line
        ? this.productsData.items.map((product) => {
          return {
            ...product,
            image: product.image.file.url,
          };
        })
        : [];
    },
    countProducts() {
      return this.productsData ? this.productsData.pagination.total : 0;
    },
  },
  methods: {
    loadColors() {
      axios.get(`https://vue-study.skillbox.cc/api/colors?colorId=${this.productsColors}`)// eslint-disable-next-line
        .then((response) => this.productsColors = response.colorId);
    },
    loadProducts() {
      this.productsLoading = true;
      this.productsLoadingFailed = false;// eslint-disable-next-line
      clearTimeout(this.loadProductsTimer); // eslint-disable-next-line
      this.loadProductsTimer = setTimeout(() => {
        axios// eslint-disable-next-line
          .get(API_BASE_URL + '/api/products', {
            params: {
              page: this.page,
              limit: this.productsPerPage,
              categoryId: this.filterCategoryId,
              minPrice: this.filterPriceFrom,
              maxPrice: this.filterPriceTo,
              colorId: this.filterColorId,
            },
          })// eslint-disable-next-line
        .then((response) => this.productsData = response.data)// eslint-disable-next-line
          .catch(() => this.productsLoadingFailed = true)// eslint-disable-next-line
          .then(() => this.productsLoading = false);
      }, 0);
    },
  },
  watch: {
    filterColorId() {
      this.loadProducts();
    },
    page() {
      this.loadProducts();
    },
    filterCategoryId() {
      this.loadProducts();
    },
    filterPriceFrom() {
      this.loadProducts();
    },
    filterPriceTo() {
      this.loadProducts();
    },
  },
  created() {
    this.loadProducts();
  },
};
</script>
